<template>
  <div class="scoreBox">
    <el-table :data="scoreForm" border style="width: 100%">
      <el-table-column prop="school_year" label="" width="180">
      </el-table-column>
      <el-table-column prop="semester" label="" width="180">
      </el-table-column>
      <el-table-column prop="class_number" label="" width="180">
      </el-table-column>
      <el-table-column prop="class_name" label=""> </el-table-column>
      <el-table-column prop="class_kind" label="" width="180">
      </el-table-column>
      <el-table-column prop="class_score" label="" width="180">
      </el-table-column>
      <el-table-column prop="make_up_exam_score " label="">
      </el-table-column>
      <el-table-column prop="retake_result" label="" width="180">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      scoreForm: [
        {
          school_year: "",
          semester: "",
          class_number: "",
          class_name: "",
          class_kind: "",
          class_score: "",
          make_up_exam_score: "",
          retake_result: "",
        },
      ],
    };
  },
  created(){
    this.initial()
  },
  methods:{
    initial(){
      let role = JSON.parse(sessionStorage.getItem("role"))
      if(role!='0'){
        this.$router.push('/login')
        this.$message.error('')
      }
    }
  }
};
</script>

<style>
</style>


