<template>
  <div class="loginBox">
    <div class="loginMainBox">
      <div class="loginbrand">
        <div class="usernameform">
          <el-form label-width="80px" :model="loginform">
            <el-form-item label=":">
              <el-input
                placeholder=""
                v-model="loginform.username"
                clearable
              >
              </el-input>
            </el-form-item>
            <el-form-item label=":">
              <el-input
                placeholder=""
                v-model="loginform.password"
                clearable
                show-password
              >
              </el-input>
            </el-form-item>

            <el-form-item label=":">
              <div class="keyimgbox">
                <!-- placeholder  -->
                <el-input
                  :placeholder="postString"
                  v-model="loginform.checkKey"
                  clearable
                  style="width: 50%"
                >
                </el-input>
                <!-- <span style="width:50%;" :v-html="checkImg">{{checkImg}}</span> -->
                <span class="keyimg" @click="checkKey()">
                  <img src="" id="keyImg" />
                </span>
              </div>
            </el-form-item>
          </el-form>
          <div class="remeberMe">
            <el-checkbox v-model="loginform.checked"></el-checkbox>
          </div>
          <div class="login_register">
            <el-button
              type="warning"
              style="margin-right: 40px"
              @click="tohome()"
              ></el-button
            >
            <el-button type="warning"></el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QS from "qs";
export default {
  data() {
    return {
      checkImg: "",
      loginform: {
        username: "",
        password: "",
        time: "",
        checkKey: "",

        checked: false,
      },
    };
  },
  created() {
    this.checkKey();
    setInterval(() => {
      this.checkKey();
    }, 60000);
  },
  computed: {
    postString() {
      return this.loginform.time;
    },
  },
  methods: {
    checkKey() {
      //  const params = new URLSearchParams
      //  params.append('key',value)
      this.$http({
        method: "get",
        url: "/user/getImgCode",
        //header:{main.js}
        //token:main.js{}
        //paramsparams()
      }).then((res) => {
        console.log(res);
        //
        //res console.logres res.flag  or  res.data....
        let key = document.getElementById("keyImg"); //id=keyImg Dom
        key.src = res.data.data.img; // id=keyImg Dom src 
        let uuid = res.data.data.uuid;
        localStorage.setItem("uuid", uuid); //

        if (localStorage.getItem("token")&&localStorage.getItem("check")) {
          this.$http({
            method:"post",
            url:"/user/loginByToken"
          }).then(res=>{
             if (res.data.data.role == "0") {
            //(role)
            this.$router.push("/home");
          } else if (res.data.data.role == "1") {
            this.$router.push("/teacherHome");
          } else if (res.data.data.role == "2") {
            this.$router.push("/rooterHome");
          }
          this.$message.success({
            message:"",
            duration:"1000"
          })
          })
        }
      });
      console.log("");
      // this.$message.success({
      //   message:"",
      //   duration:500
      //   })
    },
    async tohome() {
      //
      localStorage.setItem("check",this.checked)
      let uuid = localStorage.getItem("uuid"); //
      let data = {
        // post 
        code: this.loginform.checkKey,
        password: this.loginform.password,
        username: this.loginform.username,
      };
      this.$http({
        method: "post",
        url: "/user/login",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded", //main.js
          checkCode: uuid, //
        },
        data: QS.stringify(data), //QS  import QS from 'qs'
      }).then((res) => {
        if (res.data.flag !== true) {
          this.checkKey();
          return this.$message.error(res.data.msg);
        } else {
          let token = res.data.data.token;
          localStorage.setItem("token", token);
          this.$message.success({
            message: "",
            duration: 700,
          });
          if (res.data.data.role == "0") {
            //(role)
            this.$router.push("/home");
          } else if (res.data.data.role == "1") {
            this.$router.push("/teacherHome");
          } else if (res.data.data.role == "2") {
            this.$router.push("/rooterHome");
          }
          window.sessionStorage.setItem("Id", res.data.data.id); //session
          window.sessionStorage.setItem("role", res.data.data.role); //session
        }
      });
    },
  },
};
</script>

<style>
* {
  margin: 0;
}
body {
  background-image: url("../assets/loginBG.jpg");
}
.loginBox {
  width: 100%;
  height: 100%;
}

.loginMainBox {
  width: 60%;
  height: 568px;
  margin-top: 5%;
  margin-left: 20%;
  /* border: 1px solid red; */
  background: url("../assets/login.png") no-repeat;
  background-size: 100%;
}
.loginbrand {
  display: flex;
  flex-direction: column;
  position: relative;
  left: 48%;
  /* border: solid 1px red; */
  height: 100%;
  width: 52%;
}
.loginbrand .passward {
  display: flex;
  flex-direction: row;
}
.usernameform {
  position: relative;
  top: 30%;
  left: 13%;
  width: 350px;
  /* border: 1px solid red; */
}
.keyimgbox {
  display: flex;
  flex-direction: row;
}
.keyimg {
  width: 50%;
  /* float:left; */
}
.keyimg img {
  width: 100%;
}
.remeberMe {
  padding-left: 30%;
  position: relative;
  top: -10%;
}
.login_register {
  padding-left: 25%;
}
</style>


